.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-height: 44px;
  padding: 0 32px;
  color: $c-white;
  font-weight: 700;
  font-size: 15px;
  text-align: center;
  text-transform: uppercase;
  background: $c-green;
  border-radius: 5px;
  transition: background .3s;

  &:hover {
    background: $c-green-hover;
  }

  @include laptop {
    padding: 0 16px;
    font-size: 14px;
  }

  @include tablet {
    min-height: 40px;
  }
}
