.about-partners {
  &__wrap {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -16px;

    @include laptop {
      margin: 0 -8px -30px;
    }

    @include phone-big {
      display: block;
      margin: 0 0 -20px;
    }
  }

  &__item {
    flex: 0 1 25%;
    padding: 0 16px 30px;

    @include laptop {
      padding: 0 8px 30px;
    }

    @include tablet {
      flex: 0 1 50%;
    }

    @include phone-big {
      width: 100%;
      max-width: 350px;
      margin: 0 auto;
      padding: 0 0 20px;
    }
  }

  &__img {
    position: relative;
    height: 280px;
    margin: 0 0 25px;
    background: $c-grey;

    @include laptop-big {
      height: 220px;
    }

    @include laptop {
      margin: 0 0 15px;
    }

    @include tablet {
      height: 280px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__name {
    margin: 0 0 15px;
    font-weight: 500;
    font-size: 20px;

    @include laptop {
      margin: 0 0 10px;
      font-size: 18px;
    }

    @include tablet {
      line-height: 1.4;
    }

    @include phone {
      font-size: 16px;
    }
  }

  &__info {
    font-size: 15px;
    line-height: 2;

    @include laptop {
      line-height: 1.7;
    }

    @include tablet {
      line-height: 1.5;
    }

    @include phone {
      font-size: 14px;
    }
  }
}
