.contacts {
  &__title {
    margin: 0 0 35px;
    font-weight: 500;
    font-size: 20px;

    @include laptop {
      margin: 0 0 20px;
      font-size: 18px;
    }

    @include phone-big {
      font-size: 16px;
    }
  }

  &__wrap {
    margin: 0 0 60px;

    @include laptop {
      margin: 0 0 40px;
    }

    @include phone-big {
      margin: 0 0 30px;
    }
  }

  &__item {
    display: flex;
    align-items: flex-start;

    &:not(:last-of-type) {
      margin: 0 0 40px;

      @include laptop {
        margin: 0 0 20px;
      }

      @include tablet {
        margin: 0 0 12px;
      }
    }

    @include tablet {
      line-height: 1.35;
    }
  }

  &__item-icon {
    flex-shrink: 0;
    max-width: 20px;
    margin: 5px 17px 0 0;

    @include phone {
      max-width: 15px;
      margin: 3px 10px 0 0;
    }
  }

  &__item-wrap {}

  &__item-info {
    font-weight: 500;
    font-size: 20px;

    .link {
      color: $c-black;

      &:hover {
        color: $c-green-hover;
      }
    }

    @include laptop {
      font-size: 18px;
    }

    @include tablet {
      font-size: 16px;
    }

    @include phone-big {
      font-size: 14px;
    }
  }

  &__mail {
    padding-bottom: 2px;
    color: $c-black;
    font-weight: 500;
    line-height: 1.3;
    border-bottom: 1px solid $c-green;

    &:hover {
      border-color: transparent;
    }
  }

  &__map {
    width: 100%;
    height: 904px;
  }
}
