.environment-contacts {
  &__wrap {
    padding: 60px 72px 60px 40px;
    background: $c-grey;
    border-radius: 5px;

    @include laptop {
      padding: 40px;
    }

    @include tablet {
      padding: 30px;
    }

    @include phone-big {
      padding: 20px;
    }
  }

  &__item {
    display: flex;
    justify-content: space-between;

    &:not(:last-of-type) {
      margin: 0 0 40px;

      @include tablet {
        margin: 0 0 20px;
      }
    }

    @include phone-big {
      display: block;
    }
  }

  &__info {
    flex: 1 1 auto;
    padding: 0 20px 0 0;

    @include phone-big {
      padding: 0;
    }
  }

  &__item-content {
    display: inline-block;
    color: $c-black;
    font-weight: 500;
    font-size: 20px;

    @include laptop {
      font-size: 18px;
    }

    @include tablet {
      display: block;
      font-size: 16px;
    }

    @include phone {
      font-size: 14px;
    }
  }

  &__numbers {
    flex: 0 0 312px;

    @include laptop {
      flex: 0 0 200px;
    }
  }

  &__tel {}
}
