.home-slider {
  padding: 80px 0;

  @include laptop {
    padding: 60px 0;
  }

  @include tablet {
    padding: 40px 0;
  }

  @include phone {
    padding: 30px 0;
  }

  &__slider {
    margin-bottom: 40px;

    @include tablet {
      margin-bottom: 30px;
    }

    @include phone {
      margin-bottom: 20px;
    }
  }

  &__item {}

  &__date {
    margin: 0 0 10px;
    font-size: 15px;

    @include laptop {
      font-size: 14px;
    }

    @include tablet {
      margin: 0 0 7px;
    }

    @include phone {
      margin: 0 0 3px;
    }
  }

  &__btn-wrap {
    text-align: center;
  }
}
