.burger-anchor {

  @include tablet {
    position: relative;
    display: block;
    width: 40px;
    height: 40px;
    margin: 0 0 0 auto;
    padding: 0 5px;
    background: transparent;
    border: none;
  }

  &__span {
    @include tablet {
      position: absolute;
      top: 50%;
      left: 50%;
      display: block;
      width: 30px;
      height: 2px;
      background: $c-black;
      transform: translate(-50%, -50%);
      transition: cubic-bezier(0.6, -0.28, 0.735, 0.045) .15s all;
    }
  }

  &__span:nth-child(1) {
    @include tablet {
      transform: translate(-50%, calc(-50% - 8px));
    }
  }

  &__span:nth-child(2) {
    @include tablet {
      transform: translate(-50%, calc(-50%));
    }
  }

  &__span:nth-child(3) {
    @include tablet {
      transform: translate(-50%, calc(-50% + 8px));
    }
  }

  &-active {

    @include tablet {
      position: fixed;
      z-index: 1030;
      transform: none;
    }

    .burger-anchor {
      &__span:nth-child(1) {
        @include tablet {
          transform: translate(-50%, -50%) rotate(45deg);
        }
      }

      &__span:nth-child(2) {
        @include tablet {
          display: none;
        }
      }

      &__span:nth-child(3) {
        @include tablet {
          transform: translate(-50%, -50%) rotate(-45deg);
        }
      }
    }
  }
}

.burger-active {
  &.header__menu {
    @include tablet {
      left: 0;
      transition: cubic-bezier(0.6, -0.28, 0.735, 0.045) .3s all;
    }
  }
}
