.footer {
  padding: 30px 0;
  color: $c-white;
  font-size: 15px;
  background: $c-green url(../img/footer-circles.svg) no-repeat center/cover;

  @include tablet {
    padding: 20px 0;
  }

  &__wrap {
    display: flex;
    align-items: flex-start;

    @include tablet {
      display: block;
    }
  }

  &__col {
    margin: 0 20px 0 0;

    @include tablet {
      margin: 0 0 20px;
    }
  }

  &__logo {
    display: inline-block;
    width: 282px;
    height: 105px;
    margin: 0 0 20px;

    @include laptop {
      width: 210px;
      height: 78px;
      margin: 0 0 10px;
    }

    @include tablet {
      width: 170px;
      height: 62px;
      margin: 0;
    }
  }

  &__text {
    @include tablet {
      display: none;
      line-height: 1.3;
    }

    &--mob {
      display: none;

      @include tablet {
        display: block;
      }
    }
  }


  &__info {
    margin: 0 70px 0 auto;

    @include tablet {
      margin: 0 0 20px;
    }
  }

  &__info-item {
    display: flex;
    align-items: flex-start;

    &:not(:last-of-type) {
      margin: 0 0 20px;
    }
  }

  &__info-item-icon {
    flex-shrink: 0;
    max-width: 20px;
    margin: 5px 17px 0 0;

    @include phone {
      max-width: 15px;
      margin: 3px 10px 0 0;
    }
  }

  &__info-item-wrap {}

  &__info-link {
    padding: 0 0 2px;
    color: $c-white;
    font-weight: 500;
    font-size: 17px;
    border-bottom: 1px solid transparent;
    transition: border .3s;

    &:hover {
      border-color: $c-white;
    }
  }

  &__map {
    display: block;
    flex-shrink: 0;
    width: 140px;
    color: $c-white;
    font-weight: 500;
    text-align: center;

    &:hover {
      .footer__map-text {
        border-color: transparent;
      }
    }

    @include tablet {
      margin: 0 0 20px;
    }
  }

  &__map-img {
    width: 140px;
    height: 140px;
    margin: 0 0 12px;
    object-fit: cover;
    border-radius: 50%;
  }

  &__map-text {
    padding: 0 0 2px;
    border-bottom: 1px solid $c-white;
    transition: border .3s;
  }

}
