.accessories {
  &__wrap {
    margin: 0 0 60px;

    @include laptop {
      margin: 0 0 40px;
    }

    @include phone-big {
      margin: 0 0 30px;
    }
  }

  &__item {
    display: flex;
    align-items: flex-start;

    @include tablet {
      display: block;
    }

    &:not(:last-of-type) {
      margin: 0 0 115px;

      @include laptop {
        margin: 0 0 60px;
      }

      @include phone-big {
        margin: 0 0 30px;
      }
    }
  }

  &__item-img {
    flex: 0 1 50%;
    margin: 0 32px 0 0;

    @include tablet {
      margin: 0 0 20px;
    }
  }

  &__item-info {
    flex: 0 1 50%;

    ul {
      margin: 0 0 20px;

      @include phone-big {
        margin: 0 0 12px;
      }
    }

    li {
      position: relative;
      padding: 0 0 0 40px;

      @include tablet {
        padding: 0 0 0 20px;
      }

      &:not(:last-of-type) {
        margin: 0 0 16px;

        @include tablet {
          margin: 0 0 10px;
        }
      }

      &::before {
        position: absolute;
        top: 10px;
        left: 0;
        width: 8px;
        height: 8px;
        background: $c-green;
        border-radius: 50%;
        content: '';

        @include laptop {
          top: 8px;
        }
      }
    }
  }

  &__item-title {
    margin: 0 0 15px;
    font-weight: 500;
    font-size: 20px;

    @include laptop {
      font-size: 18px;
    }

    @include tablet {
      margin: 0 0 10px;
    }

    @include phone {
      font-size: 16px;
      line-height: 1.4;
    }
  }
}
