.faq {

  &__title {}

  &__wrap {
    margin: 0 0 50px;

    @include laptop {
      margin: 0 0 35px;
    }

    @include tablet {
      margin: 0 0 20px;
    }

    @include phone {
      margin: 0 0 15px;
    }
  }

  &__item {
    background: $c-grey;
    border-radius: 5px;

    &:not(:last-of-type) {
      margin: 0 0 20px;

      @include tablet {
        margin: 0 0 12px;
      }
    }

    &.faq__item--open {
      .faq__question-icon::after {
        transform: translateX(-50%) rotate(-90deg);
        opacity: 0;
      }

      .faq__answer {
        box-sizing: content-box;
        margin: -8px 0 0;
        padding-bottom: 10px;
        opacity: 1;

        @include tablet {
          margin: 0;
        }
      }
    }
  }

  &__question {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    min-height: 60px;
    padding: 10px 30px;
    text-align: left;

    &:hover {
      .faq__question-text {
        color: $c-green;
        text-decoration: none;
      }
    }

    @include laptop {
      min-height: 55px;
    }

    @include tablet {
      padding: 10px 20px;
    }

    @include phone {
      min-height: 50px;
      padding: 10px 15px;
    }
  }

  &__question-text {
    margin: 0 20px 0 0;
    font-weight: 500;
    font-size: 20px;
    text-decoration: underline;
    text-decoration-color: $c-green;
    transition: color .3s;

    @include laptop {
      font-size: 18px;
    }

    @include tablet {
      font-size: 16px;
    }

    @include phone {
      font-size: 14px;
    }
  }

  &__question-icon {
    position: relative;
    display: flex;
    flex-shrink: 0;
    align-items: center;
    justify-content: center;
    width: 25px;
    height: 25px;

    &::before,
    &::after {
      background: $c-green;
      border-radius: 3px;
      content: '';
    }

    &::before {
      width: 100%;
      height: 3px;
    }

    &::after {
      position: absolute;
      top: 0;
      left: 50%;
      width: 3px;
      height: 100%;
      transform: translateX(-50%);
      opacity: 1;
      transition: transform .3s, opacity .3s;
    }

    @include laptop {
      width: 22px;
      height: 22px;
    }

    @include tablet {
      width: 20px;
      height: 20px;
    }

    @include phone {
      width: 18px;
      height: 18px;
    }
  }

  &__answer {
    max-height: 0;
    padding: 0 104px 0 30px;
    overflow: hidden;
    opacity: 0;
    transition: all 0.3s ease-out;
    will-change: max-height;

    @include tablet {
      padding: 0 70px 0 20px;
    }

    @include tablet {
      padding: 0 40px 0 15px;
    }
  }

  &__info {
    padding: 15px 30px;
    color: $c-white;
    background: $c-green url(../img/faq-bg.svg) no-repeat center/cover;
    border-radius: 5px;

    @include tablet {
      padding: 10px 20px;
    }
  }

  &__info-title {
    margin: 0 0 7px;
    font-weight: 700;
    font-size: 24px;

    @include laptop {
      font-size: 20px;
    }

    @include tablet {
      margin: 0 0 3px;
      font-size: 18px;
    }

    @include phone {
      font-size: 16px;
    }
  }

  &__info-text {
    font-size: 20px;

    a {
      color: inherit;
      font-weight: 500;
      white-space: nowrap;
    }

    @include laptop {
      font-size: 18px;
    }

    @include tablet {
      font-size: 16px;
    }

    @include phone {
      font-size: 14px;
    }
  }
}
