.slider-head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 0 30px;

  @include tablet {
    margin: 0 0 20px;
  }

  @include phone {
    margin: 0 0 12px;
  }


  &__title {
    margin: 0 20px 0 0;
  }

  &__nav {
    display: flex;
    align-items: center;
  }

  &__btn {}

  &__prev {
    margin: 0 38px 0 0;
  }

  &__next {}
}
