.environment-info {
  &__item {
    &:not(:last-of-type) {
      margin: 0 0 30px;

      @include tablet {
        margin: 0 0 15px;
      }
    }
  }

  &__text {
    margin: 0 0 10px;

    p {
      &:not(:last-of-type) {
        margin: 0 0 16px;

        @include tablet {
          margin: 0 0 10px;
        }
      }
    }
  }

  &__item-info {
    display: flex;
    align-items: flex-start;
  }

  &__item-icon {
    flex-shrink: 0;
    width: 100%;
    max-width: 20px;
    margin: 8px 14px 0 0;

    @include tablet {
      margin: 4px 12px 0 0;
    }
  }

  &__item-info-text {
    font-weight: 500;
    font-size: 20px;

    @include laptop {
      font-size: 18px;
    }

    @include tablet {
      font-size: 16px;
    }

    @include phone {
      font-size: 14px;
    }
  }

  &__links {
    font-size: 17px;

    @include laptop {
      font-size: 16px;
    }

    @include tablet {
      font-size: 14px;
    }

    a {
      font-size: 20px;

      @include laptop {
        font-size: 18px;
      }

      @include tablet {
        font-size: 16px;
      }

      @include phone {
        font-size: 14px;
      }
    }
  }
}
