body {
  color: $c-text--global;
  font-weight: $f-weight--global;
  font-size: $f-size--global;
  font-family: $f-family--global;
  line-height: $l-height--global;
  background-color: $c-body-bg;


  @include laptop {
    font-size: 14px;
  }

  @include tablet {
    //  font-size: 12px;
  }

  @include phone-big {
    //  font-size: 14px;
  }
}

.slider-btn {
  flex-shrink: 0;
  width: 17px;
  height: 17px;
  transition: opacity .3s;

  img {
    width: 100%;
    height: 100%;
  }

  &:hover {
    opacity: .5;
  }
}

.section {
  padding: 0 0 80px;

  &--pb-100 {
    padding: 0 0 100px;
  }

  @include laptop {
    padding: 0 0 60px;
  }

  @include tablet {
    padding: 0 0 40px;
  }

  @include phone {
    padding: 0 0 30px;
  }
}

.more-link {
  display: inline-flex;
  align-items: center;

  &__text {
    color: $c-black;
    font-weight: 500;
    text-decoration: underline;
    transition: color .3s;
    text-decoration-color: $c-green;

    &:hover {
      color: $c-green-hover;
      text-decoration: none;
    }
  }

  &__icon {
    flex-shrink: 0;
    width: 21px;
    height: 14px;
    margin: 0 0 0 10px;
  }
}

.link {
  transition: color .3s;

  &:hover {
    color: $c-green-hover;
    text-decoration: none;
  }
}

.breadcrumbs {
  padding: 0 0 40px;

  @include laptop {
    padding: 0 0 25px;
  }

  @include phone-big {
    padding: 0 0 10px;
  }

  &__wrap {
    margin: 0 0 50px;
    padding: 35px 0;
    background: $c-green url(../img/breadcrumbs-circles.svg) no-repeat center/cover;

    @include laptop {
      margin: 0 0 30px;
      padding: 25px 0;
    }

    @include phone-big {
      margin: 0 0 20px;
      padding: 15px 0;
    }
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    color: $c-white;
    font-weight: 500;
    font-size: 15px;

    @include laptop {
      min-height: auto;
    }
  }

  &__item {
    &:not(:last-of-type) {
      &::after {
        margin: 0 15px;
        color: $c-white;
        content: '/';
      }
    }
  }

  &__link {
    color: $c-white;
    text-decoration: underline;
  }
}

.contacts-tel {
  font-weight: 500;

  &__link {
    display: inline-block;
    margin: 0 5px 0 0;
    color: $c-black;
    font-weight: 700;
    font-size: 24px;

    @include laptop {
      font-size: 20px;
    }

    @include tablet {
      font-size: 18px;
    }

    @include phone {
      font-size: 16px;
    }
  }
}

.page-info {
  &__wrap {
    padding: 60px 0;
    background: $c-grey;

    @include laptop {
      padding: 40px 0;
    }

    @include tablet {
      padding: 30px 0;
    }

    @include phone {
      padding: 20px 0;
    }
  }

  &__inner {
    display: flex;
    margin: 0 -50px;

    @include tablet {
      display: block;
      margin: 0;
    }
  }

  &__item {
    width: 100%;
    max-width: 620px;
    padding: 0 50px;

    @include tablet {
      padding: 0;

      &:first-of-type {
        margin: 0 0 20px;
      }
    }
  }

  &__text {
    font-size: 20px;
    line-height: 2;

    @include laptop {
      font-size: 18px;
    }

    @include tablet {
      font-size: 16px;
      line-height: 1.5;
    }

    @include phone {
      font-size: 14px;
    }

    p {
      &:not(:last-of-type) {
        margin: 0 0 16px;

        @include tablet {
          margin: 0 0 10px;
        }
      }
    }
  }
}

.grid-card {
  display: flex;
  flex-wrap: wrap;

  @include laptop {
    justify-content: space-between;
  }

  &__item {
    width: calc(33.33% - 32px * 2 / 3);
    margin: 0 32px 80px 0;

    &:nth-child(3n) {
      margin-right: 0;
    }

    @include laptop {
      width: calc(50% - 32px / 2);
      margin: 0 0 30px;
    }

    @include phone-big {
      width: 100%;
    }
  }
}

.grid-page {
  display: grid;
  grid-gap: 80px 100px;
  grid-template-columns: repeat(2, 1fr);
  max-width: 1144px;

  @include laptop {
    grid-gap: 50px 100px;
  }

  @include tablet {
    display: block;
  }

  &__item {
    @include tablet {
      &:not(:last-of-type) {
        margin: 0 0 25px;
      }
    }
  }
}

.cms-content {
  article {
    &:not(:last-of-type) {
      margin: 0 0 50px;

      @include laptop {
        margin: 0 0 25px;
      }
    }

    h2 {
      margin: 0 0 15px;
      font-weight: 500;
      font-size: 20px;

      @include laptop {
        font-size: 18px;
        line-height: 1.3;
      }

      @include tablet {
        font-size: 16px;
      }

      @include phone-big {
        margin: 0 0 8px;
      }
    }

    p {
      &:not(:last-of-type) {
        margin: 0 0 16px;

        @include tablet {
          margin: 0 0 10px;
        }
      }
    }

    li {
      position: relative;
      padding: 0 0 0 40px;

      @include tablet {
        padding: 0 0 0 20px;
      }

      &:not(:last-of-type) {
        margin: 0 0 16px;

        @include tablet {
          margin: 0 0 10px;
        }
      }

      &::before {
        position: absolute;
        top: 10px;
        left: 0;
        width: 8px;
        height: 8px;
        background: $c-green;
        border-radius: 50%;
        content: '';

        @include laptop {
          top: 8px;
        }
      }
    }
  }
}

.extra {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 30px 72px 30px 40px;
  background: $c-grey;
  border-radius: 5px;

  @include tablet {
    display: block;
    padding: 20px;
  }

  &__text {
    margin: 0 40px 0 0;
    font-weight: 500;
    font-size: 20px;

    @include laptop {
      font-size: 18px;
    }

    @include tablet {
      margin: 0 0 15px;
    }

    @include phone {
      font-size: 16px;
    }
  }

  &__btn {
    flex-shrink: 0;
  }

  &__form {
    display: flex;

    @include phone {
      flex-direction: column;
    }
  }

  &__form-input {
    flex: 0 1 352px;
    height: 44px;
    padding: 0 30px;
    background: $c-white;
    border: none;
    border-radius: 5px;

    @include tablet {
      flex: 1 1 auto;
    }

    @include phone {
      padding: 0 15px;
    }
  }

  &__form-btn {
    margin: 0 0 0 -10px;

    @include phone {
      margin: 10px 0 0;
    }
  }
}
