.about-reviews {

  &__slider {}

  &__slide {}

  &__slide-message {
    position: relative;
    min-height: 183px;
    margin: 0 0 37px;
    padding: 30px;
    background: $c-grey;
    border-radius: 5px;

    @include laptop {
      min-height: 140px;
      padding: 20px;
    }

    @include tablet {
      min-height: auto;
    }

    &::after {
      position: absolute;
      bottom: -30px;
      left: 30px;
      z-index: -1;
      width: 65px;
      height: 65px;
      background: url('../img/triangle-msg.svg') no-repeat center/cover;
      content: '';

      @include laptop {
        bottom: -20px;
      }
    }
  }

  &__slide-user {
    position: relative;
    min-height: 59px;
    padding: 0 0 0 72px;
  }

  &__slide-avatar {
    position: absolute;
    top: 0;
    left: 0;
    display: inline-flex;
    flex-shrink: 0;
    align-items: center;
    justify-content: center;
    width: 59px;
    height: 59px;
    background: $c-green;
    border-radius: 50%;

    @include laptop {
      top: -5px;
    }

    &::before {
      width: 25px;
      height: 29px;
      background: url('../img/avatar.svg') no-repeat center/cover;
      content: '';
    }
  }

  &__slide-name {
    font-weight: 500;
  }

  &__slide-city {
    font-size: 15px;

    @include laptop {
      font-size: 14px;
    }
  }
}
