.home-contacts {
  padding: 60px 0;
  background: $c-grey;

  @include tablet {
    padding: 40px 0;
  }

  @include phone {
    padding: 30px 0;
  }

  &__wrap {
    display: flex;

    @include tablet {
      display: block;
    }
  }

  &__content {
    display: flex;
    flex: 1 1 auto;
    flex-direction: column;
    justify-content: space-between;
    margin: 0 32px 0 0;

    @include tablet {
      margin: 0 0 20px;
    }

    @include phone {
      margin: 0 0 15px;
    }
  }

  &__item {
    display: flex;
    align-items: flex-start;

    &:not(:last-of-type) {
      margin: 0 0 45px;

      @include laptop {
        margin: 0 0 20px;
      }

      @include tablet {
        margin: 0 0 12px;
      }
    }

    @include tablet {
      line-height: 1.35;
    }
  }

  &__item-icon {
    flex-shrink: 0;
    max-width: 20px;
    margin: 5px 17px 0 0;

    @include phone {
      max-width: 15px;
      margin: 3px 10px 0 0;
    }
  }

  &__item-wrap {}

  &__item-head {
    margin: 0 0 10px;

    @include laptop {
      margin: 0 0 5px;
    }

    @include tablet {
      br {
        display: none;
      }
    }
  }

  &__map {
    width: 100%;
    max-width: 592px;
    height: 440px;

    @include laptop {
      max-width: 450px;
      height: 350px;
    }

    @include tablet {
      max-width: 100%;
    }

    @include phone {
      height: 270px;
    }
  }

}
