.container {
    position: relative;
    width: 100%;
    max-width: #{$xxl-container};
    margin: 0 auto;
    padding: 0 12px;
    @include laptop-big {
        max-width: #{$xl-container}
    }
    @include laptop {
        max-width: #{$lg-container}
    }
    @include tablet {
        max-width: #{$md-container}
    }
    @include phone-big {
        max-width: #{$sm-container}
    }
    @include phone {
        max-width: #{$xs-container}
    }
}
