.environment-links {
  &__item {
    display: inline-flex;
    align-items: center;
    color: $c-black;
    font-weight: 500;
    font-size: 20px;

    @include laptop {
      font-size: 18px;
    }

    @include tablet {
      font-size: 16px;
    }

    @include phone {
      font-size: 14px;
    }

    &:not(:last-of-type) {
      margin: 0 0 30px;

      @include laptop {
        margin: 0 0 20px;
      }

      @include phone-big {
        margin: 0 0 10px;
      }
    }

    &::before {
      flex-shrink: 0;
      width: 26px;
      height: 29px;
      margin: 0 30px 0 0;
      background: url('../img/download.svg') no-repeat center/cover;
      content: '';

      @include laptop {
        margin: 0 15px 0 0;
      }
    }
  }
}
