.about-main {
  &--mb-60 {
    margin: 0 0 60px;

    @include laptop {
      margin: 0 0 40px;
    }

    @include tablet {
      margin: 0 0 20px;
    }
  }

  &--mb-80 {
    margin: 0 0 80px;

    @include laptop {
      margin: 0 0 60px;
    }

    @include tablet {
      margin: 0 0 40px;
    }

    @include phone {
      margin: 0 0 30px;
    }
  }

  &__wrap {}

  &__title {
    margin: 0 0 15px;
    font-weight: 500;
    font-size: 20px;

    @include laptop {
      font-size: 18px;
    }

    @include tablet {
      margin: 0 0 10px;
    }

    @include phone {
      font-size: 16px;
      line-height: 1.4;
    }
  }

  &__subtitle {
    margin: 0 0 30px;
  }

  &__text-big {
    font-size: 20px;
    line-height: 2;

    @include laptop {
      font-size: 18px;
      line-height: 1.5;
    }

    @include tablet {
      font-size: 16px;
      line-height: 1.3;
    }

    @include phone {
      font-size: 14px;
    }
  }

  &__list {
    margin: 15px 0 0;
  }

  &__list-item {
    position: relative;
    padding: 0 0 0 40px;

    @include tablet {
      padding: 0 0 0 20px;
    }

    &:not(:last-of-type) {
      margin: 0 0 16px;

      @include tablet {
        margin: 0 0 10px;
      }
    }

    &::before {
      position: absolute;
      top: 10px;
      left: 0;
      width: 8px;
      height: 8px;
      background: $c-green;
      border-radius: 50%;
      content: '';

      @include laptop {
        top: 8px;
      }
    }
  }

  &__title-video {
    margin: 0 0 30px;
  }

  &__video {
    position: relative;

    &::after {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(0deg, rgb(0 0 0 / 40%), rgb(0 0 0 / 40%));
      content: '';
    }
  }

  &__video-bg {
    width: 100%;
  }

  &__video-play {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 1;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 59px;
    height: 59px;
    background: $c-green;
    border-radius: 50%;
    transform: translate(-50%, -50%);
    transition: background .3s;

    &:hover {
      background: $c-green-hover;
    }


  }

  &__video-play-icon {
    width: 21px;
    height: 24px;
    margin: 0 0 0 6px;
  }
}
