.header {
  padding: 35px 0 20px;

  @include laptop {
    padding: 25px 0 15px;
  }

  @include phone {
    padding: 15px 0 10px;
  }

  &__main {
    display: flex;
    align-items: center;
    margin: 0 0 22px;

    @include laptop {
      margin: 0 0 16px;
    }
  }

  &__logo {
    flex-shrink: 0;
    width: 282px;
    height: 102px;
    margin: 0 102px 0 0;

    @include laptop-big {
      margin: 0 40px 0 0;
    }

    @include laptop {
      width: 210px;
      height: 78px;
    }

    @include tablet {
      width: 170px;
      height: 62px;
      margin: 0 20px 0 0;
    }
  }

  &__content {
    flex-grow: 1;

    @include tablet {
      display: none;
    }

    &--mob {
      display: none;

      @include tablet {
        display: block;
      }
    }
  }

  &__view {
    display: flex;
    align-items: center;
    margin: 0 0 19px;
    color: $c-green;
    font-weight: 700;
    font-size: 16px;
    text-transform: uppercase;

    @include laptop {
      margin: 0 0 8px;
      font-size: 14px;
    }

  }

  &__view-icon {
    flex-shrink: 0;
    width: 40px;
    height: 27px;
    margin: 0 16px 0 0;

    @include laptop {
      width: 28px;
      height: 20px;
      margin: 0 12px 0 0;
    }
  }

  &__wrap {
    display: flex;
    justify-content: space-between;

    @include tablet {
      flex-wrap: wrap;
      margin: 0 0 -12px;
    }
  }

  &__tel {
    display: inline-flex;
    align-items: center;
    margin: 0 20px 0 0;
    color: $c-black;
    font-size: 26px;
    line-height: 1.54;

    @include laptop {
      font-size: 18px;
    }

    @include tablet {
      margin: 0 20px 8px 0;
      font-size: 16px;
    }
  }

  &__tel-icon {
    flex-shrink: 0;
    width: 17px;
    height: 17px;
    margin: 0 10px 0 0;
  }

  &__mail {
    display: inline-flex;
    align-items: center;
    margin: 0 20px 0 auto;
    color: $c-black;
    font-weight: 500;
    text-decoration: underline;
    text-decoration-color: $c-green;

    @include tablet {
      margin: 0 20px 12px 0;
    }
  }

  &__mail-icon {
    flex-shrink: 0;
    width: 19px;
    height: 15px;
    margin: 0 14px 0 0;
  }

  &__btn {
    margin: 0 0 0 auto;

    @include tablet {
      margin: 0 0 12px;
    }
  }

  &__menu {
    @include tablet {
      position: fixed;
      top: 0;
      bottom: 0;
      left: calc(100vw + 50px);
      z-index: 1010;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
      width: 100vw;
      height: 100vh;
      margin: 0;
      padding: 0 190px 0 12px;
      padding-top: 50px;
      overflow-y: scroll;
      line-height: 1.3;
      background-color: $c-white;
      transition: cubic-bezier(0.6, -0.28, 0.735, 0.045) .15s all;
    }

    @include phone {
      padding: 0 50px 0 12px;
      padding-top: 40px;
    }
  }

  &__nav {
    @include tablet {
      margin: 0 0 15px;
    }
  }

  &__nav-list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 -17px;

    @include laptop-big {
      margin: 0 -8px;
    }

    @include laptop {
      margin: 0 -4px;
    }

    @include tablet {
      flex-direction: column;
      align-items: flex-start;
      margin: 0;
    }
  }

  &__nav-item {
    display: inline-flex;
    align-items: center;
    padding: 0 17px;

    @include laptop-big {
      padding: 0 8px;
    }

    @include laptop {
      padding: 0 4px;
    }

    @include tablet {
      padding: 0;

      &:not(:last-of-type) {
        margin: 0 0 12px;
      }
    }
  }

  &__nav-item-submenu {
    position: relative;
    display: flex;
    align-items: center;

    &.active {
      .header__nav-submenu {
        transform: translate(0, 0);
        visibility: visible;
        opacity: 1;
        pointer-events: all;
      }
    }
  }

  &__nav-burger {
    flex-shrink: 0;
    width: 20px;
    height: 15px;
    margin: 0 9px 0 0;
  }

  &__nav-link {
    color: $c-black;
    font-weight: 700;
    font-size: 15px;
    text-transform: uppercase;

    @include laptop {
      font-size: 13px;
    }

    @include tablet {
      font-size: 14px;
    }
  }

  &__nav-submenu {
    position: absolute;
    top: calc(100% + 20px);
    left: 0;
    z-index: 2;
    min-width: 466px;
    padding: 10px 25px 28px;
    background: $c-white;
    box-shadow: 0 40px 40px rgb(0 0 0 / 10%);
    transform: translate(0, 10%);
    visibility: hidden;
    opacity: 0;
    transition: opacity .3s, visibility .3s, transform .3s;
    pointer-events: none;

    @include laptop {
      top: calc(100% + 15px);
    }

    @include tablet {
      top: calc(100% + 5px);
      padding: 10px 15px 20px;
    }

    @include phone {
      left: -12px;
      min-width: 350px;
    }
  }

  &__nav-submenu-item {
    &:not(:last-of-type) {
      margin: 0 0 14px;

      @include phone {
        margin: 0 0 8px;
      }
    }
  }

  &__nav-submenu-link {
    color: $c-black;
    font-weight: 500;
    font-size: 17px;
    text-decoration: underline;
    text-decoration-color: $c-green;

    @include tablet {
      font-size: 16px;
    }
  }
}
