.title {
  margin: 0 0 30px;
  font-weight: 700;
  font-size: 40px;
  line-height: 1;

  &--main {
    margin: 0;
  }

  @include laptop {
    margin: 0 0 22px;
    font-size: 32px;
  }

  @include tablet {
    margin: 0 0 15px;
    font-size: 28px;
  }

  @include phone {
    margin: 0 0 12px;
    font-size: 24px;
  }
}
