.environment-main {
  &__title {
    margin: 0 0 15px;
    font-weight: 500;
    font-size: 20px;

    @include laptop {
      font-size: 18px;
    }

    @include phone {
      font-size: 16px;
    }
  }

  &__subtitle {
    margin: 0 0 80px;
    font-size: 20px;
    line-height: 2;

    @include laptop {
      margin: 0 0 60px;
      font-size: 18px;
      line-height: 1.5;
    }

    @include tablet {
      margin: 0 0 30px;
      font-size: 16px;
      line-height: 1.3;
    }

    @include phone {
      font-size: 14px;
    }
  }

  &__wrap {}

  &__item {
    min-width: 0;
  }

  &__item-text {
    &:not(:last-of-type) {
      margin: 0 0 16px;

      @include tablet {
        margin: 0 0 10px;
      }
    }

    &:first-of-type {
      &::before {
        display: block;
        width: 59px;
        height: 59px;
        margin: 0 0 20px;
        background: $c-green url('../img/check.svg') no-repeat center/27px 20px;
        border-radius: 50%;
        content: '';

        @include laptop {
          width: 50px;
          height: 50px;
          background: $c-green url('../img/check.svg') no-repeat center/20px 15px;
        }

        @include tablet {
          width: 40px;
          height: 40px;
          margin: 0 0 8px;
          background: $c-green url('../img/check.svg') no-repeat center/16px 10px;
        }
      }
    }
  }

  &__item-slider {
    position: relative;
    margin: 0 -30px;

    @include phone {
      margin: 0;
    }
  }

  &__item-slide {
    margin: 0 30px;

    @include phone {
      margin: 0;
    }
  }

  &__item-slide-btn {
    position: absolute;
    top: 50%;
    z-index: 1;
    transform: translateY(-50%);
  }

  &__item-slide-prev {
    left: 0;
  }

  &__item-slide-next {
    right: 0;
  }

  &__item-img {
    margin: 0 auto;
  }
}
