.environment-route {
  &__wrap {}

  &__info {}

  &__title {
    margin: 0 0 15px;
    font-weight: 500;
    font-size: 20px;

    @include laptop {
      margin: 0 0 10px;
      font-size: 18px;
    }

    @include phone {
      font-size: 16px;
    }
  }

  &__text {
    p {
      &:not(:last-of-type) {
        margin: 0 0 16px;

        @include tablet {
          margin: 0 0 10px;
        }
      }
    }
  }

  &__map {}
}
