*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 100vw;
  min-width: 320px;
  margin: 0;
  overflow-x: hidden;
  transition: cubic-bezier(0.165, 0.84, 0.44, 1) .2s all;
  text-rendering: optimizeLegibility;
  text-decoration-skip: objects;
  text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: transparent;
}

main {
  width: 100%;
}

h1 {
  margin-top: 0;
}

a {
  text-decoration: none;

  &:hover,
  &:focus,
  &:active {
    text-decoration: none;
  }
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

li {
  list-style-type: none;
}

iframe {
  border: 0;
}

textarea {
  overflow: auto;
  vertical-align: top;
  box-shadow: none;
  resize: none;
}

button {
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

input,
textarea {
  outline: none;
}
