.product {
  &__wrap {
    display: flex;

    @include tablet {
      display: block;
    }
  }

  &__gallery {
    display: flex;
    flex: 0 0 50%;
    min-width: 0;
    margin: 0 32px 0 0;

    @include laptop {
      flex: 0 0 60%;
    }

    @include tablet {
      margin: 0 0 25px;
    }

    @include phone {
      display: block;
    }
  }

  &__thumbs {
    flex-shrink: 0;
    order: -1;
    width: 155px;
    height: 433px;
    margin: 0 21px 0 0;

    @include phone {
      width: 100%;
      height: auto;
      margin: 15px 0 0;
    }
  }

  &__thumb {
    cursor: pointer;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__slider-btn {
    position: absolute;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 40px;
    background: rgba($color: $c-white, $alpha: 50%);
    transition: background .3s;

    &:hover {
      background: rgba($color: $c-white, $alpha: 80%);
    }

    @include tablet {
      width: 40px;
      height: 100%;

      img {
        transform: rotate(-90deg);
      }
    }
  }

  &__slider-prev {
    top: 0;

    @include tablet {
      left: 0;
    }
  }

  &__slider-next {
    bottom: 0;

    @include tablet {
      right: 0;
    }
  }

  &__slider {
    height: 433px;
  }

  &__slide {
    position: relative;
    display: flex;
    align-items: center;
    height: 100%;
    border: 1px solid #F0F0F0;

    &::before {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      width: 60px;
      height: 60px;
      background: url('../img/zoom.svg') no-repeat center/cover;
      content: '';
    }
  }

  &__slide-img {
    width: 100%;
  }

  &__parameter {
    display: flex;
    flex: 0 1 50%;
    flex-direction: column;

    @include laptop {
      flex: 0 1 40%;
    }
  }

  &__title {
    margin: 0 0 20px;
  }

  &__parameter-list {
    margin: auto 0 0;
  }

  &__parameter-list-item {
    &:not(:last-of-type) {
      margin: 0 0 5px;
    }
  }

  &__parameter-list-item-name {
    font-weight: 700;
  }

  &__parameter-colors {
    display: flex;
    flex-wrap: wrap;
    max-width: 540px;
    margin: 10px -10px -10px;

    @include laptop {
      margin: 5px -5px -5px;
    }
  }

  &__parameter-colors-item {
    width: 34px;
    height: 34px;
    margin: 0 10px 10px;
    border: 3px solid $c-white;
    border-radius: 50%;
    cursor: pointer;

    @include laptop {
      margin: 0 5px 5px;
    }

    &--white {
      background: $c-white;
      border: 1px solid #F0F0F0;
    }

    &--black {
      background: $c-black;
    }

    &--brown {
      background: #623535;
    }

    &--red {
      background: #CF2525;
    }

    &--blue-dark {
      background: #05556F;
    }

    &--blue {
      background: #45B9DE;
    }

    &--blue-white {
      background: #AADEEE;
    }

    &--grey-dark {
      background: #969696;
    }

    &--grey {
      background: #D7D7D7;
    }

    &--yellow {
      background: #FFCB67;
    }
  }

  &__descr {}

  &__descr-title {
    margin: 0 0 5px;
  }

  &__descr-content {
    margin: 0 0 40px;

    @include tablet {
      margin: 0 0 20px;
    }
  }

  &__descr-back {}

  &__descr-back-icon {
    flex-shrink: 0;
    width: 24px;
    height: 14px;
    margin: 0 10px 0 0;
  }
}
