.banners {
  &__slider {
    position: relative;
  }

  &__item {
    position: relative;

    @include tablet {
      background: $c-green;
    }
  }

  &__bg-circles {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;

    @include tablet {
      display: none;
    }
  }

  &__bg {
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
    width: 56%;
    min-width: 750px;
    height: 100%;
    object-fit: cover;

    @include tablet {
      display: none;
    }
  }

  &__item-wrap {
    position: relative;
    width: 100%;
    max-width: 488px;
    min-height: 380px;
    padding: 20px 0 55px;

    @include laptop {
      min-height: 350px;
      padding: 20px 0 45px;

      &::before {
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        width: 100%;
        height: 100%;
        background: $c-green;
        content: '';
      }
    }

    @include tablet {
      max-width: 100%;
      min-height: auto;
    }
  }

  &__title {
    margin: 0 0 5px;
    color: $c-white;
    font-weight: 500;
    font-size: 30px;
    line-height: 1.5;

    &--ml {
      display: block;
      margin: 0 0 0 40px;

      @include laptop {
        margin: 0 0 0 20px;
      }
    }

    b {
      font-weight: 700;
      font-size: 40px;

      @include laptop {
        font-size: 32px;
      }

      @include phone-big {
        font-size: 26px;
      }
    }

    @include laptop {
      font-size: 26px;
    }

    @include phone-big {
      font-size: 22px;
      line-height: 1.35;
    }
  }

  &__item-img {
    max-width: 477px;

    @include laptop {
      max-width: 350px;
    }

    @include tablet {
      max-width: 300px;
      margin: 0 0 0 auto;
    }

    @include phone-big {
      max-width: 270px;
    }
    @include phone {
      max-width: 240px;
    }
  }

  &__nav {
    position: absolute;
    bottom: 20px;
    left: calc((100vw - 1216px) / 2);
    z-index: 1;
    display: flex;
    align-items: center;

    @include laptop-big {
      left: calc((100vw - 1116px) / 2);
    }

    @include laptop {
      bottom: 15px;
      left: calc((100vw - 936px) / 2);
    }

    @include tablet {
      left: 50%;
      transform: translateX(-50%);
    }
  }

  &__slider-btn {}

  &__slider-prev {}

  &__slider-pagination {
    display: flex;
    height: 8px;
    margin: 0 35px;

    .swiper-pagination-bullet {
      display: inline-block;
      width: 8px;
      height: 8px;
      background: #3BA449;
      border-radius: 50%;
      cursor: pointer;
      transition: background .3s;

      &:hover {
        background: #AADFB1;
      }

      &:not(:last-of-type) {
        margin: 0 24px 0 0;
      }
    }

    .swiper-pagination-bullet-active {
      background: #AADFB1;
    }
  }

  &__slider-next {}
}
