// Modern format (woff + ttf)
@mixin font($family, $filePath, $weight: normal, $style: normal) {

    @font-face {
        font-weight: $weight;
        font-family: $family;
        font-style: $style;
        src:
            url("#{$filePath}.woff") format("woff"), // Prety modern browsers
            url("#{$filePath}.ttf") format("truetype"); // Safari, Android, iOS

        font-display: swap;

    }

}



@include font('Geometria', '../fonts/Geometria/Geometria', normal);
@include font('Geometria', '../fonts/Geometria/Geometria-Medium', 500);
@include font('Geometria', '../fonts/Geometria/Geometria-Bold', 700);
