.how {
  padding: 60px 0 50px;
  background: $c-grey;

  @include tablet {
    padding: 40px 0;
  }

  @include phone {
    padding: 30px 0;
  }

  &__title {}

  &__subtitle {
    margin: 0 0 50px;
    font-size: 20px;

    @include laptop {
      margin: 0 0 35px;
      font-size: 18px;
    }

    @include tablet {
      margin: 0 0 20px;
      font-size: 16px;
    }

    @include phone {
      margin: 0 0 15px;
    }
  }

  &__wrap {
    display: flex;
    justify-content: space-between;
    margin: 0 -20px;

    @include tablet {
      display: block;
      margin: 0;
    }
  }

  &__item {
    display: flex;
    align-items: flex-start;
    max-width: 430px;
    padding: 0 20px;

    &:last-of-type {
      max-width: 360px;
    }

    @include laptop-big {
      display: block;

      &:last-of-type {
        max-width: 430px;
      }
    }

    @include tablet {
      display: flex;
      align-items: center;
      max-width: 100%;
      padding: 0;

      br {
        display: none;
      }

      &:last-of-type {
        max-width: 100%;
      }

      &:not(:last-of-type) {
        margin: 0 0 12px;
      }
    }

    @include phone {
      display: block;
    }
  }

  &__item-icon {
    flex-shrink: 0;
    max-width: 94px;
    margin: 12px 15px 0 0;

    @include laptop-big {
      display: flex;
      align-items: center;
      justify-content: center;
      max-width: 70px;
      height: 50px;
      margin: 0 0 10px;
    }

    @include tablet {
      max-width: 50px;
      margin: 0 12px 0 0;
    }

    @include phone {
      max-width: 40px;
      height: 40px;
      margin: 0 0 3px;
    }
  }

  &__item-wrap {}

  &__item-text {
    margin: 0 0 15px;
    font-weight: 500;
    font-size: 20px;

    @include laptop {
      font-size: 18px;
    }

    @include tablet {
      margin: 0 0 5px;
      font-size: 16px;
    }

    @include phone {
      line-height: 1.2;
    }
  }
}
