.card {
  &--services {
    display: flex;
    flex-direction: column;
    padding: 30px 30px 20px;
    background: $c-grey;
    border-radius: 5px;

    .card__more {
      margin: auto 0 0;
    }

    @include phone-big {
      padding: 20px;
    }
  }

  &__img {
    position: relative;
    display: block;
    height: 0;
    margin: 0 0 30px;
    padding-bottom: 67%;

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    @include phone-big {
      margin: 0 0 15px;
    }
  }

  &__name {
    margin: 0 0 20px;
    color: $c-green;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.88;
    text-transform: uppercase;
    transition: color .3s;

    &:hover {
      color: $c-green-hover;
    }

    @include laptop {
      line-height: 1.2;
    }

    @include tablet {
      font-size: 15px;
    }

    @include phone {
      font-size: 14px;
    }
  }

  &__text {
    margin: 0 0 20px;

    @include tablet {
      margin: 0 0 10px;
    }
  }

  &__more {}
}
